<template>
  <div class="page-wrapper">

    <jumbotron :variant="jumbotronData">
      <div class="container" slot="content">
        <h1 class="jumbotron-heading">Jumbotron auto</h1>
        <p class="lead">This can be called by name: jumbotron-auto</p>
      </div>
    </jumbotron>

    <div class="container">
        <div class="row">
            <div class="ml-3 mb-5 bg-success text-white p-2" slot="content">
                <h1>Dynamicly loading content</h1>
                <p>Loads 100 new items everytime you reach the bottom of the page.</p>
                <p>Loading from <a class="text-white" target="_blank" href="https://jsonplaceholder.typicode.com/">jsonplaceholder.typicode.com</a></p>
            </div>
        </div>
    </div>

    
    <div class="container">
        <div class="row">
            <text-picture slot="content" v-for="content in contents" :key="content.id" :variant="textVariant">
                <h3 slot="title">{{ content.title }} <small>{{ date }}</small> </h3>
                <p slot="text">{{ content.body }}</p>
            </text-picture>
        </div>
    </div>

  </div>
</template>

<script>

import { loadMoreData, Appear } from '@/assets/js/main.js';

export default {

  name: 'about',

  data () {
    return {

      jumbotronData: {
        name: 'jumbotron-auto',
        src: 'https://d193o8p26ehxdy.cloudfront.net/img-thumbs/960w/LBOGQME99N.jpg',
      },
      
      textVariant: {
        type: 'text-only',
        grid: 'col-md-6'
      },

      contents: [],

      newDate: new loadMoreData({})


    }
  },
  methods: {

    loadMore: function() {
      this.newDate.render( () => {
        this.$http.get('https://jsonplaceholder.typicode.com/posts').then( res => {
          res.body.forEach( (item) => {
            // keeps adding more data to page FOREVER!
            item.id = this.contents.length + item.id++ + this.newDate.timesFired;
            this.contents.push( item );
          }); 
          this.newDate.isRequesting = false;
          this.newDate.timesFired++;

        });
      });
    }


  },

  computed: {

    date: function() {

      let d = new Date(),
          month = d.getUTCMonth() + 1;

      return d.getUTCDate() + '/' + month + '/' + d.getUTCFullYear();

    }


  },


  beforeDestroy() {

    // remove scroll loading data 
    document.removeEventListener('scroll', this.loadMore);

  },


  created(){

    // first loaded content 
    this.$http.get('https://jsonplaceholder.typicode.com/posts').then( res => {

      this.contents = res.body;

    });

    // adds new data when scrolling
    document.addEventListener('scroll', this.loadMore);


    // make each loaded item pop in
    let appearEffect = new Appear({
      element: '.col-md-6',
      addClass: 'appear-effect'
    });



  },

  route: {

    canReuse: false

  }
}
</script>



<style lang="scss">

  .col-md-6 {
    opacity: 0;
    transition: all 0.4s ease;
    transform: translate3d(0px, 50px, 0px);
  }

  .col-md-6.appear-effect, .col-md-6:nth-of-type(-n+4) {
    opacity: 1;
    transform: translate3d(0px, 0px, 0px);
  }

</style>



